body{
    background: rgb(2, 0,36);
    background: radial-gradient(circle, rbga92,0,36,1) 0%;
}

.container {
    display: flex !important;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.container > form {
    width: 70%;
    border: 1px solid #dfdfdf;
    padding: 20px;
    border-radius: 5px;
    background: #fff;
}

pre {
    width: 70%;
    color: white;
    font-size: large;
}

button{
    background-color: #0563b4;
}

p{
    color: red;
}